.social {
	display: flex;
	justify-content: center;
}
.social a {
	margin: 1rem 1rem;
}
.social svg {
	width: 30px;
	height: 30px;
	fill: #c4c4c4;
	transition: fill 2s var(--snap);
	cursor: pointer;
}
.social svg:hover {
	fill: #000;
}



